import React, {useCallback} from "react";
import style from "./BoothTime.module.scss";
import {useDispatch, useSelector} from "react-redux";
import {setTimeCheck} from "../../../../../store/redusers/boothReduser";

export const BoothTime = ({itemId}) => {
    const dispatch = useDispatch()

    const inputs = useSelector(state => state.booth.boothTime)
    const timeCheck = useCallback((check, id) => dispatch(setTimeCheck(check, id)),[dispatch])

 console.log(inputs)
    return (
        <>
            <p className="lightGrey">Укажите время аренды</p>
            <div className={style.boothTime}>
                {
                    inputs.map(i =>
                        <div key={i.id}>
                            <input
                                type="radio"
                                id={"time" + i.id + itemId}
                                name={"time" + itemId}
                                value={i.name}
                                onClick={(e) => timeCheck(e.target.checked, i.id)}
                            />
                            <label
                                htmlFor={'time' + i.id + itemId}
                                key={i.id}>{i.name}
                            </label>
                        </div>
                    )
                }
            </div>
        </>
    )
}
